.header {
    justify-content: center;
    align-items: center;
    padding: 0 40px;

    height: 50px;
    background-color: #192227;
}

.header__menu {
    display: flex;
    justify-content: space-between;
    align-items: center;

    height: 50px;

    color: #ffffff;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 24px;
}

.header__menu-buttons {
    display: flex;

    justify-content: space-between;

    height: 50px;
    width: 200px;
}

.header__menu-buttons a {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 50px;
    width: 50px;

    /* color: #ffffff;
    font-size: 48px; */
    text-decoration: none;
}

/* .header__menu-buttons a:hover {
    color: #7b61ff;
    border: 1px solid #7b61ff;
} */

.btn {
    display: flex;

    border: 1px solid #ffffff;
}

.btn:hover {
    display: flex;

    border: 1px solid #7b61ff;
}

.btn-add {
    background: no-repeat center/cover url("../../components/btn-add/plus.svg");
}

.btn-add:hover {
    background: no-repeat center/cover url("../../components/btn-add/plus-hover.svg");
}

.btn-list {
    background: no-repeat center/cover url("../../components/btn-list/stack.svg");
}

.btn-list:hover {
    background: no-repeat center/cover url("../../components/btn-list/stack-hover.svg");
}

.btn-search{
background: no-repeat center/cover url("../../components/btn-search/magnifying-glass.svg");
}

.btn-search:hover {
    background: no-repeat center/cover url("../../components/btn-search/magnifying-glass-hover.svg");
}