.modal-content-button {
    display: flex;
    justify-content: center;
    align-items: center;

    flex-basis: 200px;

    height: 50px;

    appearance: none;

    border: 1px solid #000000;
    border-radius: 0;

    font-size: 24px;
    font-weight: bold;

    text-align: center;
}
.modal-content-button:hover {
    background: rgb(123, 60, 255);
}
.button_cancel:hover {
    background-color: #cccccc;
}
.modal-content-button:focus {
    outline: none;
    box-shadow: 0 0 0 1px #edf5fa;
}
.button_add {
    background-color: #7b61ff;

    color: #ffffff;
    text-shadow: 0px 1px 0 #000000, 0px -1px 0 #000000, 1px 0px 0 #000000, -1px 0px 0 #000000, 1px 1px 0 #000000,
        1px -1px 0 #000000, -1px 1px 0 #000000, -1px -1px 0 #000000;
}
