.data_of_exp-item, 
.data_of_exp-item-active {
    display: flex;
    flex-direction: column;

    padding: 0;
    font: inherit;
    background-color: transparent;

    height: 100px;
    width: 150px;

    color: #000;
    font-size: 15px;
    font-weight: bold;

    border: 2px solid rgba(123, 97, 255, 0);

    cursor: default;
    user-select: none;

    appearance: none;
    border-radius: 0;
    outline: none;
}
.data_of_exp-item:hover {
    border: 2px solid #7b61ff;
}

.data_of_exp-item-active .data_of_exp-item-content {
    background-color: #7b61ff;

    color: #ffffff;
    text-shadow: 0px 1px 0 #000000, 0px -1px 0 #000000, 1px 0px 0 #000000, -1px 0px 0 #000000, 1px 1px 0 #000000,
        1px -1px 0 #000000, -1px 1px 0 #000000, -1px -1px 0 #000000;
}
.data_of_exp-item-title {
    background-color: #daebf7;
}
.data_of_exp-item-content {
    background-color: #edf5fa;
}
.data_of_exp-item-title,
.data_of_exp-item-content {
    display: flex;
    justify-content: center;
    align-items: center;

    text-align: center;

    height: 50px;
    width: 146px;

    border: 1px solid #000;
}
.data_of_exp-item-big_word {
    font-size: 14px;
}
.data_of_exp-item-big_words {
    font-size: 12px;
    font-weight: bold;
}
