.main__pagination-list-item,
.main__pagination-list-item-active,
.main__pagination-list-item-hidden {
    display: flex;

    font-size: 24px;

    height: 25px;
    width: 50px;

    justify-content: center;
    align-items: center;

    background-color: #fff;
    border: 1px solid #000;

    cursor: default;
    user-select: none;
}

.main__pagination-list-item-hidden{
    background: none;
    border: none;
}

.main__pagination-list-item:hover {
    background-color: #81a0a7;
}

.main__pagination-list-item-active {
    color: #fff;
    background-color: #7b61ff;
}
