.modal-content{
    display: flex;
    align-items: center;
    flex-direction: column;
    flex-grow: 1;

    background-color: #263238;

    padding: 25px;

    font-size: 18px;
}
.modal-content-info{
    display: flex;
}
.modal-content-buttons{
    display: flex;
    justify-content: space-between;
    flex-basis: 50px;

    margin: 25px 0 0 0;

    width: 420px;
}
.modal-content-wrapper{
    display: flex;
    flex-basis: 725px;
    justify-content: space-between;
    flex-wrap: wrap;

    max-height: 725px;

    padding: 0 25px;
}