.modal-content-select-list-item,
.modal-content-select-list-item-active {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 50px;

    color: #000;
    font-size: 15px;
    font-weight: bold;

    text-align: center;

    cursor: default;
    user-select: none;

    min-height: 50px;
    width: 250px;

    background-color: #edf5fa;

    border: 1px solid #000;
}
.modal-content-select-list-item:hover {
    border: 3px solid #7b61ff;
}

.modal-content-select-list-item-active {
    background-color: #7b61ff;

    color: #ffffff;
    text-shadow: 0px 1px 0 #000000, 0px -1px 0 #000000, 1px 0px 0 #000000, -1px 0px 0 #000000, 1px 1px 0 #000000,
        1px -1px 0 #000000, -1px 1px 0 #000000, -1px -1px 0 #000000;
}
.modal-content-select-list-item-input {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 244px;

    height: 44px;

    color: #000;
    font-size: 15px;
    font-weight: bold;

    text-align: center;

    cursor: default;

    border: 0;
    outline: none;
}
