.modal-content-select-list{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    flex-basis: 250px;
    /* justify-content: center; */
    align-items: center;

    font-size: 14px;

}
.modal-content-select-list-elems{
    max-height: 550px;
    width: 250px;
    overflow-y: auto;
    overflow-x: hidden;
}

.modal-content-select-list-elems::-webkit-scrollbar{
    /* display: none; */
    /* position: absolute; */
    /* left: 0; */
    /* z-index: 5000; */
    width: 5px;
}
.modal-content-select-list-elems::-webkit-scrollbar-track{
    background-color: #daebf7;
    border: 1px solid #000;
}
.modal-content-select-list-elems::-webkit-scrollbar-thumb{
    background-color: #7b61ff;
}
