.modal-container {
    display: flex;
    flex-direction: column;

    height: 900px;
    width: 1350px;

    position: fixed;
    z-index: 20;

    margin: auto;
    
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    color: #ffffff;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 24px;
}
