.modal-content-data_of_research_result {
    display: flex;
    justify-content: space-between;
    align-content: flex-start;
    flex-wrap: wrap;

    flex-basis: 500px;

    margin: 25px 0 0 0;
}
.modal-content-data_of_research_result-title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    flex-basis: 500px;

    height: 50px;

    text-align: center;
}